import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import styles from './usersEditModal.module.scss'
import { deleteTitlesData,updateTitlesData } from '../../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import { TaskContext } from '../../../components/Context/TaskContext';
import { useContext,useState } from "react"
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2)
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1)
  }
}));

export interface DialogTitleProps {
  id: string;
  children?: React.ReactNode;
  onClose: () => void;
}


function BootstrapDialogTitle(props: DialogTitleProps) {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: theme => theme.palette.grey[500]
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
}
interface Props {
 open :boolean ;
 handleClose() :void ;
 role : any ;
 reload() : Promise<void>
}

const UsersEditModal: React.FC<Props> = ({open,handleClose,role,reload}) => {
  const { token } = useContext(TaskContext);
  const [selectValue,setSelectValue] = useState<string>(role.role);

 const updateThisUser = async () : Promise<void> =>{
 await updateTitlesData({role : selectValue}, role.id,'/api/v1/user',token);
  handleClose();
  reload();
 }

 const deleteUser = () : void =>{
  if (window.confirm("Do you really want to delete this user?")) {
    deleteTitlesData(role.id,'/api/v1/user',token);
    handleClose();
    reload();
  };

 };
 
  return (
    <div>
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <BootstrapDialogTitle
          id="customized-dialog-title"
          onClose={handleClose}
        >
          Edit User
        </BootstrapDialogTitle>
        <DialogContent dividers>
         <div >
         <div className={styles.role}><span>Role:</span>
         <select value={selectValue} onChange={(e)=>setSelectValue(e.target.value)}>
          <option selected={role.role === 'user' ? true : false} value='user'>user</option>
          <option selected={role.role === 'admin' ? true : false} value='admin'>admin</option>
          <option selected={role.role === 'SuperAdmin' ? true : false} value='SuperAdmin'>SuperAdmin</option>
          </select>
          </div>
          <div><button onClick={deleteUser} className={styles.delete}>Delete</button>(User will be deleted)</div>   
         </div>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={updateThisUser} >
            Save changes
          </Button>
        </DialogActions>
      </BootstrapDialog>
    </div>
  );
};
export default UsersEditModal;
