import { useState, useEffect } from 'react';
import styles from './usersAdmin.module.scss';
import { useContext } from 'react';
import { TaskContext } from '../../components/Context/TaskContext';
import UsersEditModal from './UsersEditModal/UsersEditModal';
import PopUp from './PopUp/PopUp';

const UsersAdmin : React.FC  = () => {
  async function getData(url :string, token :string) : Promise<any> {
    let metod = {
      method: 'GET',
      headers: {
        authorization: 'Bearer' + ' ' + token
      }
    };
    try {
      const response = await fetch(url, metod) ;
      let apiData = await response.json();
      return apiData;
    } catch (err) {
      console.log('Errror with dataBases');
    }
  }
  interface Users {
    id : string ;
    email :string ;
    role :string ;
    nick : string ;
  }

  const [users, setUsers] = useState<Users[]>([]);
  const [open,setOpen] = useState<boolean>(false)
  const { token } : any = useContext(TaskContext);
  const {isAdmin} : any = useContext(TaskContext);
  const [role,setRole] = useState<string>('')
  const handleClose =() : void =>{
    setOpen(false)
  }

 const openThis = async (e:any) =>{
  setRole(e)
  setOpen(true)
 }

  async function reload() : Promise<void> {
    getData('/api/v1/user', token).then(data => setUsers(data));
  }

  useEffect(() => {
    reload();
  }, []);

  
    return (
      <div className={styles.EditUsersWrapper}>
        <h1>Edit Users </h1>
        {users && users?.map((el : Users) => {
          return (
            <div key={el.id} className={styles.tableUsers} onClick={()=>{openThis(el)}}>
              <div>
                <p>E-mail : {el.email}</p>
                <p>Staus : {el.role}</p>
                <p>Nick Name: {el.nick}</p>
              </div>
            </div>
          );
        })}
     {isAdmin=== 'SuperAdmin' ? <UsersEditModal open={open} handleClose={handleClose} role={role} reload={reload}/> :  <PopUp open={open} handleClose={handleClose}  />}
      </div>
    );
  
};

export default UsersAdmin;
